/* global jQuery */

jQuery(($) => {
	$('.file-download--wrapper .file-download--date').each((index, element) => {
		const filteredFileDate = $.trim($(element).text().split('ver=')[1]);
		const fileDate = filteredFileDate.split(/[-]/);
		const fileDay = fileDate[2];
		const fileMonth = fileDate[1];
		const fileYear = fileDate[0];

		function GetMonthName(monthNumber) {
			const months = [
				'January',
				'February',
				'March',
				'April',
				'May',
				'June',
				'July',
				'August',
				'September',
				'October',
				'November',
				'December',
			];
			return months[monthNumber - 1];
		}

		const dateDisplay = `${GetMonthName(
			fileMonth
		)} ${fileDay}, ${fileYear}`;
		$(element).html(dateDisplay);
	});
});
